// components/FeatureCard.tsx
interface FeatureCardProps {
    title: string;
    content: string;
    className?: string;
  }
  
  export default function FeatureCard({ title, content, className }: FeatureCardProps) {
    return (
      <div className={`p-6 rounded-xl ${className || 'bg-white'}`}>
        <h3 className="text-xl font-bold text-amber-800 mb-4">{title}</h3>
        <p className="text-gray-600">{content}</p>
      </div>
    );
  }